/*.App {
  text-align: center;
}*/

body {
  margin: 0;
}
.App-body{
 align-content:center;
 align-items:center;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /*background-color: #282c34;*/
  background-color: #61dafb;
  max-height: 5.5vh;
  align-content: center;
  align-items:center;
  margin: 0;
  font-size: 12px;
  padding: 14px;
  color: white;
}

.App-link {
  color: #61dafb;
}
.messagebox{
	border: 0.5px solid grey;
}
.messagebox:hover{
	color: white;
	background-color:grey
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#user_window{
  color: #61dafb;
}

.sub-title{
  box-sizing: border-box;
  -moz-box-sizing:border-box;
  padding: 0;
  margin: 0;
  width:100%;
}
#subtitle-main{
 box-sizing:border-box;
 -moz-box-sizing:border-box;
 width:50%;
 float:left;
 border-right: 0.5px solid grey;
 border-left: 0.5px solid grey;
 border-bottom: 0.5px solid grey;
 padding:10px;
}
#webpage-name{
 box-sizing:border-box;
 -moz-box-sizing:border-box;
 width:20%;
 float:left;
 border-bottom: 0.5px solid grey;
 padding:10px;
}
#time{
 box-sizing:border-box;
 -moz-box-sizing:border-box;
 width:20%;
 float:left;
 border-bottom: 0.5px solid grey;
 padding:10px;
}
#welcome-notice{
  font-size: 16px;
  text-align:center;
}
.App-main{
  box-sizing: border-box;
  -moz-box-sizing:border-box;
  padding: 0;
  margin: 0;
  width:100%;
  align-content: center;
  align-items:center;
}
#column-left{
	box-sizing:border-box;
	-moz-box-sizing:border-box;
	width:20%;
	float:left;
	border-right: 0.5px solid grey;
	padding: 0,0,0,0;
	margin: 0;
}
#column-middle{
	box-sizing:border-box;
	-moz-box-sizing:border-box;
	width:50%;
	float:left;
	height: 80vh;
	padding: 0,0,0,0;
	margin: 0;
	overflow-y: scroll;
}
#column-right{
	box-sizing:border-box;
	width:20%;
	float:left;
	border-left: 0.5px solid grey;
	padding: 0,0,0,0;
	margin: 0;
}
#message {
    border: 2px solid grey;
    border-radius: 0.5em;
    font-size: larger;
    width:99%
}
#chat{
	overflow-y: scroll;
	flex: 1;
	list-style: none;
	padding-bottom:0;
	margin-bottom:0;
}
#message:focus {
    outline: none;
}
#user_list{
	list-style: none;
}
footer{
line-height: 100px;
height: 100px;
}
footer{
width: 100%;
position: absolute;
bottom: 0
}
